import React from 'react';
import _ from 'lodash';

import './Board.css';
import './DeckConstruction.css';
import './Multiplayer.css';

export function Multiplayer(props) {
  return <div className="board">
    <div className="multiplayer-intro">欢迎来到多维合作模式！</div>
    <button className="multiplayer-btn" onClick={props.create_room}>创建房间</button>
    <button  className="multiplayer-btn" onClick={props.enter_room}>加入房间</button>
    <button className="multiplayer-back-btn" onClick={props.back}>返回</button>
  </div>
}

export function CreateRoom(props) {
  return <div className="board">
    <button className="difficulty-btn" onClick={props.enter_difficulty(4)}>普通</button>
    <button className="difficulty-btn" onClick={props.enter_difficulty(5)}>高压</button>
    <button className="difficulty-btn" onClick={props.enter_difficulty(6)}>危急</button>
    <button className="multiplayer-back-btn" onClick={props.back}>返回</button>
  </div>
}

export function EnterRoom(props) {
  return <div className="board">
    <form>
        请输入房间号:&nbsp;
        <input 
          type = "text"
          value = {props.value} 
          onChange = {props.handleChange} 
          className = "input-deck-name"
        />
      </form>
      <button onClick={props.enter_room} className="enter-room" > 进入房间 </button>
      <button onClick={props.back} className="multiplayer-back-btn" > 返回 </button>
  </div>
}

export function get_room_id(difficulty) {
  let date = new Date();
  return "" + (date.getMinutes()+10) + (date.getSeconds()+10) + difficulty + _.random(9);
}